/** @jsx React.DOM */var React = require('react/addons')
var Bootstrap = require('react-bootstrap')


module.exports = React.createClass({

  displayName: 'page',

  render: function(){
    var Navbar  = Bootstrap.Navbar
      , Nav     = Bootstrap.Nav
      , NavItem = Bootstrap.NavItem;

    var toggle = (
      React.DOM.button({type: "button", className: "navbar-toggle", 'data-toggle': "collapse", 'data-target': "#navbar-collapse"}, 
        React.DOM.span({className: "sr-only"}, "Toggle navigation"), 
        React.DOM.span({className: "icon-bar"}), 
        React.DOM.span({className: "icon-bar"}), 
        React.DOM.span({className: "icon-bar"})
      )
    )
    return this.transferPropsTo(
      Navbar({fixedTop: true, activeKey: this.props.page, toggleNavKey: 0, toggleButton: toggle}, 
          Nav({role: "navigation", key: 0}, 
            NavItem({key: "docs", href: "#"}, "Docs"), 
            NavItem({key: "dl", href: "#"}, "Download"), 
            NavItem({key: "github", href: "https://github.com/theporchrat/react-widgets"}, "Github")
          )
      )
    )
  }
})


